<template>
    <div>
        <div class="wrapper">
            <div class="tabItem" :class="{selected:isCustom}" @click="isCustom=true">自定义账号</div>
            <div class="tabItem" :class="{selected:!isCustom}" @click="isCustom=false">批量生成账号</div>
        </div>
        <div class="custom-wrapper" v-if="isCustom">
            <table>
                <tr>
                    <td>用户名</td>
                    <td><input type="text" v-model="userName"/></td>
                </tr>
                <tr>
                    <td>密码</td>
                    <td><input type="password" v-model="password"/></td>
                </tr>
                <tr>
                    <td></td>
                    <td>
                        <button @click="addAccount">提交</button>
                    </td>
                </tr>
            </table>
        </div>
        <div v-else>
            <div class="custom-wrapper">
                <label>新增账号个数</label> <input type="text" v-model="accountNum" style="width: 30px">

                <button @click="AddCountInbulk">提交</button>

            </div>

            <div v-if="newAccounts.length>0" >
                <h2>新增账号如下:</h2>
                <table class="newAccounts">
                    <tr>
                        <th>账号</th>
                        <th>密码</th>
                    </tr>

                    <tr v-for="(account,index) in newAccounts">
                        <td>{{account.account}}</td>
                        <td>{{account.password}}</td>
                    </tr>

                </table>
            </div>


        </div>

    </div>
</template>

<script>

    import fetch from "../api/fetch";
    import parseResponse from "../lib/parseResponse"

    export default {
        name: "Add",
        data() {
            return {
                isCustom: true,
                userName: "",
                password: "",

                accountNum: 0,
                newAccounts: []
            }
        },
        // 在切换时注意重置不相干数据
        watch: {
            isCustom() {
                if (this.isCustom) {
                    this.accountNum = 0
                } else {
                    this.userName = ""
                    this.password = ""
                }
            }
        },

        methods: {
            async addAccount() {
                const res = await fetch('register', {userName: this.userName, password: this.password})
                const {code} = res
                if (code == 9) {
                    window.alert("添加账号成功！")
                    this.userName = ""
                    this.password = ""
                } else {
                    window.alert("账号已存在！")
                }
            },
            async AddCountInbulk() {
                const res = await fetch('generateBunchOfAccounts', {accountNum: this.accountNum})

                this.newAccounts = parseResponse(res)

                setTimeout(() => {
                    this.newAccounts = []
                }, 5000)

            }
        }
    }
</script>

<style scoped lang="scss">
    .wrapper {
        position: relative;
        display: flex;
    }

    .tabItem {
        position: relative;
        padding: 10px;
    }

    .selected::after {
        content: '';
        position: absolute;
        left: 25%;
        bottom: 0px;
        border-bottom: 5px solid rgba(135, 80, 1, 1);
        width: 50%;
        border-radius: 3px;
    }

    .custom-wrapper {
        padding: 10px;
        display: inline-block;
        border: 1px solid black;
    }


    table.newAccounts {
        border-collapse: collapse;
        border-spacing: 0;
        border: 1px solid grey;

        td, th {
            border: 1px solid grey;
        }

    }


</style>