<template>
    <div>

        <div>
            <input class="searchInput" type="text" placeholder="请输入账户相关信息" v-model="searchInfo"/>
        </div>


        <table>
            <tr>
                <th>账号</th>
                <th>密码</th>
                <th>授权截至日期</th>
                <th>CPU序列号</th>
                <th>主板序列号</th>
                <th>MAC地址</th>
                <th>硬盘序列号</th>
                <th>操作</th>
            </tr>

            <tr v-for="(account,index) in accoutList">
                <td>{{account.userName}}</td>
                <td>{{account.password}}</td>
                <td>{{account.accreditDeadline}}</td>
                <td>{{account.cpuId}}</td>
                <td>{{account.motherBoardId}}</td>
                <td>{{account.macAddress}}</td>
                <td>{{account.diskId}}</td>
                <td style="padding: 10px">
                    <button @click="deleteAccount(account.userName)">删除</button>
                    <button>
                        <router-link :to="{name: 'Update', params:{userName: account.userName}}">
                            修改
                    </router-link> </button>
                </td>
            </tr>

        </table>


    </div>


</template>

<script>
    import fetch from "../api/fetch";
    import parseResponse from "../lib/parseResponse"

    export default {
        name: "Search",

        data() {
            return {
                searchInfo: "",
                accoutList: []
            }
        },
        async created() {
            this.accoutList = await this.getAllAccounts()

        },


        computed: {},

        watch: {
            async searchInfo() {
                this.accoutList = await this.getAccountList()
            }


        },
        methods: {

            async getAccountList() {
                if (this.searchInfo == "") {
                    return await this.getAllAccounts()
                } else {
                    return await this.getAccountListBySearch()
                }
            },

            async getAllAccounts() {
                const result = await fetch('getAllUserInfo', {secretCode: "open sesame"})
                return parseResponse(result)
            },
            async getAccountListBySearch() {
                const result = await fetch('showAccountInfo', {namePieces: this.searchInfo})
                return parseResponse(result)
            },
            async deleteAccount(userName) {

                const confirm = window.confirm(`确认删除账户:${userName}吗？`)

                if (confirm) {
                    const result = await fetch('deleteAccount', {userName})
                    if (result.code == 33) {
                        window.alert("删除成功！")
                        this.accoutList = await this.getAccountList()

                    }
                }
            }

        }

    }
</script>

<style scoped lang="scss">
    table {
        border-collapse: collapse;
        border-spacing: 0;
        border: 1px solid grey;
    }

    td, th {
        border: 1px solid grey;
        text-align: center;
    }

    .searchInput{
        margin: 10px;
        height: 20px;
        font-size: 20px;
        padding: 5px;
    }

</style>