<template>

    <table style="border: 1px solid black;margin: 10px">
        <tr>
            <td>用户名</td>
            <td><input type="text" v-model="userName"/></td>
        </tr>
        <tr>
            <td>密码</td>
            <td><input type="text" v-model="password"/></td>
        </tr>
        <tr>
            <td>剩余授权时长</td>
            <td style="display: flex;align-items: center">


                    <span v-if="!changeable&&initialDays<0">
                        <span style="color: red" v-if="initialDays==-1">已过期！<button
                                @click="changeable=true">延长授权期限</button> </span>
                        <span style="color: red" v-else-if="initialDays==-2">未授权！<button
                                @click="changeable=true">授权</button> </span>

                    </span>
                <span v-else>
                        <input type="text" v-model="leftDays" style="width: 20px"/>天
                        <input type="text" v-model="leftHours" style="width: 20px"/>小时
                        <input type="text" v-model="leftMinutes" style="width: 20px"/>分钟
                    </span>

            </td>
        </tr>
        <tr>
            <td></td>
            <td>
                <button @click="submit">提交</button>
            </td>
        </tr>
    </table>
</template>

<script>
    import fetch from "../api/fetch";
    import parseResponse from "../lib/parseResponse"

    export default {
        name: "Update",
        data() {
            return {
                userName: "",
                password: "",
                leftDays: 0,
                leftHours: 0,
                leftMinutes: 0,

                initialDays: 0,
                initialHours: 0,
                initialMinutes: 0,

                lastSubmitUserName: "",
                changeable: false
            }
        },

        async created() {

            const {userName} = this.$route.params

            // 初始化 lastSubmitUserName
            this.lastSubmitUserName = userName


            const res = await fetch('showSpecificUserInfo', {userName})
            const accountInfo = parseResponse(res)

            console.log(accountInfo);


            // TODO:修改leftDays
            const {password, leftDays} = accountInfo

            console.log('leftDays');
            console.log(leftDays);


            this.userName = userName
            this.password = password
            this.leftDays = leftDays

            this.initialDays = leftDays


            if (leftDays > 0) {
                const {leftHours, leftMinutes} = accountInfo
                this.leftHours = leftHours
                this.leftMinutes = leftMinutes

                this.initialHours = leftHours
                this.initialMinutes = leftMinutes

            }


        },
        watch: {
            changeable() {
                if (this.changeable) {
                    this.leftDays = 0
                }
            }
        },

        methods: {
            async submit() {


                let neededData = {
                    oldUserName: this.lastSubmitUserName,
                    newUserName: this.userName,
                    newPassword: this.password,
                }

                if (this.ifChange()) {
                    neededData = {
                        ...neededData,
                        newDays: this.leftDays,
                        newHours: this.leftHours,
                        newMinutes: this.leftMinutes
                    }
                }


                console.log(neededData);

                const res = await fetch('changeInfoByoldUserName', neededData)
                const {code} = res

                if (code == 200) {
                    window.alert('修改成功！')
                } else {
                    window.alert('修改失败！')
                }

                // 提交完毕后，修改 lastSubmitUserName
                this.lastSubmitUserName = this.userName

            },

            ifChange() {
                return this.initialDays != this.leftDays || this.initialHours != this.leftHours || this.initialMinutes != this.leftMinutes
            }
        }


    }
</script>

<style scoped lang="scss">
    .wrapper {
        display: inline-block;
        border: 1px solid black;
    }
</style>