import axios from 'axios'
import url from './index'

function fetch (api, data ) {
    return new Promise((resolve, reject) => {
        axios.get(url[api], {params: data })
            .then(res => {
            // 各种业务处理
            resolve(res.data)
        }).catch(err => {
            reject(err)
        })
    })
}

export default fetch