import Vue from 'vue'
import VueRouter from 'vue-router'
import Search from '../views/Search'
import Add from '../views/Add'
import Update from '../views/Update'

Vue.use(VueRouter)

const routes = [
    {
        path: '/',
        redirect: '/search'
    },
    {
        path: '/search',
        name: 'Search',
        component: Search
    },
    {
        path: '/add',
        name: 'Add',
        component: Add
    },
    {
        path: '/update/:userName',
        name: 'Update',
        component: Update
    }
]

const router = new VueRouter({
    routes
})

export default router
